import React from 'react'

const MessageContact = () => {
	return (
		<div className="message__contact contact">
			<div className="contact__item">
				<img src="images/message/Home.svg" alt="message" className="contact__image"/>
				<div className="contact__info">
					<h4 className="contact__info-title">Location</h4>
					<p className="contact__info-text">
						324, Golden Tower, Amborkhana, Sylhet
					</p>
				</div>
			</div>
			<div className="contact__item">
				<img src="images/message/Mail_2.svg" alt="message" className="contact__image"/>
				<div className="contact__info">
					<h4 className="contact__info-title">Email</h4>
					<p className="contact__info-text">
						junaed@deviserweb.com
					</p>
				</div>
			</div>
			<div className="contact__item">
				<img src="images/message/Phone_2.svg" alt="message" className="contact__image"/>
				<div className="contact__info">
					<h4 className="contact__info-title">Toll Free Call</h4>
					<p className="contact__info-text">
						0800 234 6578
					</p>
				</div>
			</div>
		</div>
	)
}

export default MessageContact