import React from 'react'

import arrow from '../portfolio/arrow.png'

const PortfolioItem = ({
	title,
	cat,
	category,
	img,
}) => {
	return (
		<>
			<div className="portfolio__item card" cat={cat}>
				<img className="card__img" src={img} alt=""/>
				<div className="card__info">
					<h3 className="card__title">{title}</h3>
					<p className="card__cat">{category}</p>
					<button className="card__btn">View Project
						<img src={arrow} alt=""/>
					</button>
				</div>
			</div>
		</>
	)
}

export default PortfolioItem